.navbar {
  background-color: rgba(173, 216, 230, 0.956);
  height: 80px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 2em 0 1em;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: rgb(130, 130, 237);
  width: 297px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 7em;
  left: -100%;
  transition: 850ms;
  z-index: 1;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 27px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}


.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: rgb(130, 130, 237);
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}